import TournamentCard from '../TournamentCard/TournamentCard';
import './TournamentList.css';

function TournamentList({tournaments}) {

  const tournamentCards = tournaments.map((tournament) => <TournamentCard key={tournament.id} tournament={tournament}/>)

  return (
    <div className='h-layout tournament-list'>
      {tournamentCards}
    </div>
  );
}

export default TournamentList;
