import TeamCard from '../TeamCard/TeamCard';
import './TeamList.css';

function TeamList({teams, onSaveMatchCallback}) {

  const teamCards = teams.map((team, index) => <TeamCard key={team.id} team={team} onSaveMatchCallback={onSaveMatchCallback}/>);

  return (
    <div className='team-list h-layout align-left'>
      {teamCards}
    </div>
  );
}

export default TeamList;
