import { useEffect, useState } from 'react';
import { Modal } from '../../elements/Modal';
import MatchForm from '../MatchForm/MatchForm';
import MatchList from '../MatchList/MatchList';
import './TeamCard.css';

function TeamCard({team, onSaveMatchCallback}) {
  const [modalIsOpen, setIsOpen] = useState([false]);
  const [matches, setMatches] = useState([]);

  useEffect(() => {
    setMatches(team.matches);
  }, [team]);
 
  const closeModal = () => {
    setIsOpen([false]);
  };

  const onSaveMatch = (match) => {
    setMatches([...matches, match]);
    closeModal();
    onSaveMatchCallback(match);
  };

  const players = team.players.map((player, index) => <span className='mr text-sm' key={index}>{player}</span>)

  return (
    <div className='card v-layout align-stretch'>
      <button  className='btn--tertiary' onClick={() => setIsOpen([true])}>Add Match</button>
      <h4 className='text-accent'>Players</h4>
      <div className='h-layout justify-stretch'>{players}</div>

      <h4 className='text-accent'>Matches</h4>
      <MatchList matches={matches} />
      
      <Modal
          isOpen={modalIsOpen}
          onRequestClose={closeModal}
          size='sm'
        >
        <MatchForm team={team} onSave={(match) => onSaveMatch(match)} />
      </Modal>
    </div>
  );
}

export default TeamCard;
