import { useEffect, useState } from 'react';
import Services from '../../../services/Services';
import { Modal } from '../../elements/Modal';
import TournamentForm from '../TournamentForm/TournamentForm';
import TournamentList from '../TournamentList/TournamentList';
import './Home.css';

function Home() {
  const [modalIsOpen, setIsOpen] = useState([false]);
  const [tournaments, setTournaments] = useState([]);

  useEffect(() => {
    loadTournaments();
  }, [])

  const closeModal = () => {
    setIsOpen([false]);
  };

  const loadTournaments = () => {
    Services.tournaments().list().then((list) => {
      setTournaments(list);
    });
  }

  const onSaveTournaments = () => {
    loadTournaments();
    closeModal();
  }

  return (
    <div className='home v-layout'>
      <div className='flex-grow v-layout start-tournament-btn'><button onClick={() => setIsOpen([true])}>Start Tournament</button></div>
      <h3>Tournaments</h3>
      <TournamentList tournaments={tournaments}/>
      
      <Modal
          isOpen={modalIsOpen}
          onRequestClose={closeModal}
          size='sm'
        >
        <TournamentForm onSave={() => onSaveTournaments()} />
      </Modal>
    </div>
  );
}

export default Home;
