import { useEffect, useState } from 'react';
import Services from '../../../services/Services';
import { v4 as uuidv4 } from 'uuid';
import _, { set } from 'lodash';
import './Tournament.css';
import TeamList from '../TeamList/TeamList';
import Results from '../Results/Results';
import { Modal } from '../../elements/Modal';
import TeamForm from '../TeamForm/TeamForm';
import moment from 'moment';

function Tournament(props) {
  const [modalIsOpen, setIsOpen] = useState([false]);
  const [tournament, setTournament] = useState({
    name: '',
    teamSize: 0,
    players: [],
    teams: []
  });

  useEffect(() => {
    loadTournament();
  }, []);

  const closeModal = () => {
    setIsOpen([false]);
  };

  const loadTournament = () => {
    Services.tournaments().read(props.match.params.id).then((tournament) => {
      setTournament(tournament);
    });
  }

  const saveTeams = (teams) => {
    Services.tournaments().saveTeams(tournament.id, teams).then((teams) => {
      const tournamentCopy = {...tournament};
      tournamentCopy.teams = [...teams, ...tournamentCopy.teams];
      setTournament(tournamentCopy);
    });
  }

  const generateRandomTeams = () => {
    const teamCount = tournament.players.length / tournament.teamSize;

    const playersShuffled =  _.shuffle(tournament.players);
    const randomTeams = []
    for(let i = 0; i < teamCount; i++) {
      const teamPlayers = [];
      for(let j = 0; j < tournament.teamSize; j++) {
        teamPlayers.push(playersShuffled[i*tournament.teamSize + j])
      }
      randomTeams.push({
        id: uuidv4(),
        tournamentId: tournament.id,
        players: teamPlayers,
        matches: []
      })
    }
    saveTeams(randomTeams);
  }

  const onSaveTeam = (team) => {
    loadTournament();
  }

  const onSaveMatchCallback = (match) => {
    loadTournament();
  }

  const players = tournament.players.map((player, index) => {
    return (
      <span className='mr' key={index}>{player}</span>
    )
  })

  return (
    <div className='tournament v-layout'>
      <h2>{tournament.name}</h2>
      <div className='mb h-layout'>
        <div className='mr-3'><span className='text-accent text-sm'>Date</span> {moment(tournament.created).format('MM/DD/YYYY')}</div>
        <div><span className='text-accent text-sm'>Team size</span> {tournament.teamSize}</div>
      </div>
      <div className='h-layout'>{players}</div>
      <Results tournament={tournament} />
      <h3 className='mt-3'>Teams</h3>
      <div className='h-layout'>
        <button className='mr' onClick={() => generateRandomTeams()}>Generate Teams</button>
        <button onClick={() => setIsOpen([true])}>Add Team</button>
      </div>
      <TeamList teams={tournament.teams} onSaveMatchCallback={() => onSaveMatchCallback()}/>
      
      <Modal
          isOpen={modalIsOpen}
          onRequestClose={closeModal}
          size='sm'
        >
        <TeamForm tournamentId={tournament.id} players={tournament.players} teamSize={tournament.teamSize} onSave={(team) => onSaveTeam(team)} />
      </Modal>
    </div>
  );
}

export default Tournament;
