import _ from "lodash";
import mock from './mock.json';

export default class TournamentService {

  constructor(firebase) {
    if(!localStorage.getItem('tournaments')){
      localStorage.setItem('tournaments', JSON.stringify(mock));
    }
    this.firebase = firebase;
  }
  

  async save(tournament) {
    const tournaments = await this.list();
    tournaments.push(tournament);
    return this.saveList(tournaments);
  }

  async update(id, tournament) {
    const tournaments = await this.list();
    const tIndex = this.searchById(tournaments, id);
    tournaments[tIndex] = tournament;
    return this.saveList(tournaments);
  }

  async saveList(tournaments) {
    //return localStorage.setItem('tournaments', JSON.stringify(tournaments));
    return this.firebase.saveTournaments(tournaments);
  }

  async list() {
    //return JSON.parse(localStorage.getItem('tournaments'));
    return this.firebase.getTournaments();
  }

  async read(id) {
    const tournaments = await this.list();
    const tIndex = this.searchById(tournaments, id);
    return tournaments[tIndex];
  }

  async saveTeam(tournamentId, team) {
    const tournament = await this.read(tournamentId);
    if(!tournament['teams']){
      tournament['teams'] = [];
    }
    tournament['teams'] = [team, ...tournament['teams']];
    this.update(tournamentId, tournament);
    return team;
  }

  async saveTeams(tournamentId, teams) {
    const tournament = await this.read(tournamentId);
    if(!tournament['teams']){
      tournament['teams'] = [];
    }
    tournament['teams'] = [...teams, ...tournament['teams']];
    this.update(tournamentId, tournament);
    return teams;
  }

  async saveMatch(tournamentId, teamId, match) {
    const tournament = await this.read(tournamentId);
    if(!tournament['teams']){
      tournament['teams'] = [];
    }
    const tIndex = this.searchById(tournament['teams'], teamId);
    tournament['teams'][tIndex].matches.push(match);
    this.update(tournamentId, tournament);
    return match;
  }

  searchById(list, id) {
    let index = -1;
    list.forEach((item, i) => {
      if(item.id === id){
        index = i;
      }
    })
    return index;
  }

  getMatchPoint(match) {
    return this.getPositionPoint(match.position) + this.getMatchKills(match);
  }

  getMatchKills(match) {
    return _.sum(match.teamScore.map((score) => parseInt(score.kills)));
  }

  getPositionPoint(position) {
    switch(position) {
      case '1': return 20;
      case '2': return 15;
      case '3': return 10;
      case '4': return 5;
      case '5': return 3;
      case '6': return 2;
      default: return 0;
    }
  }

  getKillPoint(kills) {
    return kills;
  }

  getDamagePoint(dmg) {
    return 0;
  }

  getScorePoint(score) {
    return 0;
  }

  getResults(tournament) {
    const players = [...tournament.players];
    const playersResultObject = {};
    players.forEach((player) => {
      if(!playersResultObject[player]){
        playersResultObject[player] = {
          player: player,
          kills: 0,
          damage: 0,
          points: 0,
          total: 0
        }
      }
      tournament.teams.forEach((team) => {
        team.matches.forEach((match) => {
          if(team.players.indexOf(player) > -1){
            playersResultObject[player]['points'] += this.getPositionPoint(match.position);
            playersResultObject[player]['total'] += this.getPositionPoint(match.position);
          }
          match.teamScore.forEach((score) => {
            if(player === score.player) {
              playersResultObject[player]['kills'] += parseInt(score.kills);
              playersResultObject[player]['damage'] += parseInt(score.damage);
              playersResultObject[player]['total'] += parseInt(score.kills);
            }
          })
        })
      })
    })

    const playersResult = [];
    Object.keys(playersResultObject).forEach((key) => {
      playersResult.push(playersResultObject[key]);
    })

    return {
      players: _.orderBy(playersResult, ['total'], ['desc']),
      teams: _.orderBy(tournament.teams.map((team) => {
        const kills = _.sumBy(team.matches, (match) => _.sumBy(match.teamScore, (score) => parseInt(score.kills)));
        const points = _.sumBy(team.matches, (match) => this.getPositionPoint(match.position));
        return {
          players: team.players,
          kills: kills,
          damage: _.sumBy(team.matches, (match) => _.sumBy(match.teamScore, (score) => parseInt(score.damage))),
          points: points,
          total: kills + points
        }
      }), ['total'], ['desc'])
    };
  }

}